import React from "react";
import ReactDOM from "react-dom";
import { createBrowserHistory } from "history";
import { Router, Route, Switch } from "react-router-dom";

import "assets/scss/material-kit-react.scss?v=1.9.0";

// pages for this product
import LandingPage from "views/LandingPage/LandingPage.js";
import InstantUser from "views/InstantUserPage/InstantUser.js"
import LoginPage from "views/LoginPage/LoginPage";
import Dashboard from "views/Dashboard/Dashboard";
import Profile from "views/Profile/Profile";
import Predict from "views/Predict/Predict"
var hist = createBrowserHistory();

ReactDOM.render(
  <Router history={hist}>
    <Switch>
      <Route path="/neuralhack-react/landing-page" component={LandingPage} />
      <Route path="/neuralhack-react/login-page" component={LoginPage} />
      <Route path="/neuralhack-react/instantUser" component={InstantUser} />
      <Route path="/neuralhack-react/dashboard" component={Dashboard}/>
      <Route path="/neuralhack-react/profile" component={Profile}/>  
      <Route path="/neuralhack-react/predict" component={Predict}/>
      <Route path="/neuralhack-react" component={LandingPage} />
    </Switch>
  </Router>,
  document.getElementById("root")
);
